"use client";
import React, { useState, useEffect, useContext } from "react";
import { SuscribeContext } from "@/context/Suscribe.context";
import { useRouter } from "next/navigation";
import { ChevronLeft } from "lucide-react";

const PaymentForm: React.FC = () => {
  const { viewPlan, suscribirse } = useContext(SuscribeContext);
  const router = useRouter();

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  // Inicializamos el SDK con la public key de prueba

  const handleFormSubmit = async (formData: any) => {
    setError("");
    setLoading(true);

    if (!viewPlan) {
      setError("No se ha seleccionado un plan.");
      setLoading(false);
      return;
    }

    if (formData.error) {
      setError(formData.error.message);
      setLoading(false);
      return;
    }

    try {
      const paymentMethodToken = formData.token.id;

      // Llama a la función para suscribirse usando el token obtenido
      await suscribirse(viewPlan.id, paymentMethodToken);

      router.push("/suscribirse/success");
    } catch (err) {
      console.error("Error en handleFormSubmit:", err);
      setError("Error al procesar el pago. Por favor, intente nuevamente.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <section className="max-w-3xl px-4 py-12 mx-auto">
      <div className="p-8 bg-gray-800 shadow-2xl rounded-2xl">
        <div className="flex items-center mb-6">
          <button
            onClick={() => router.back()}
            className="p-2 mr-4 text-white bg-gray-700 rounded-full hover:bg-gray-600"
          >
            <ChevronLeft size={24} />
          </button>
          <h2 className="text-3xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-indigo-400 via-purple-400 to-pink-400">
            Completa tu Pago
          </h2>
        </div>

        {viewPlan && (
          <div className="mb-6">
            <p className="text-gray-300">
              Estás suscribiéndote al plan{" "}
              <span className="font-bold">{viewPlan.name}</span> por{" "}
              <span className="font-bold">
                ${viewPlan.precio} dólares mensuales
              </span>
              .
            </p>
          </div>
        )}

        {error && <p className="mb-4 text-red-500">{error}</p>}

        <CardForm
          onSubmit={handleFormSubmit}
          amount={viewPlan ? viewPlan.precio : 0}
          buttonText={loading ? "Procesando..." : "Confirmar Pago"}
          styles={{
            input:
              "w-full p-3 mt-1 text-white bg-gray-700 rounded-lg focus:outline-none",
            button:
              "w-full py-3 font-semibold text-white transition rounded-lg shadow-md bg-gradient-to-r from-purple-600 to-indigo-600 hover:opacity-90",
          }}
        />
      </div>
    </section>
  );
};

export default PaymentForm;
